<template>
  <div>

  <el-form :model="ruleForm" :rules="rules" style="padding-top: 100px;" ref="ruleForm" label-width="200px" class="demo-ruleForm">

    <h1>学生信息</h1>
    <el-form-item label="请输入学籍号：" prop="sid">
      <el-input v-model="ruleForm.sid"></el-input>
    </el-form-item>
    <el-form-item label="姓名：" prop="name">
      <el-input v-model="ruleForm.name"></el-input>
    </el-form-item>

    <el-form-item label="性别" prop="sex">
      <el-radio-group v-model="ruleForm.sex">
        <el-radio label="男"></el-radio>
        <el-radio label="女"></el-radio>
      </el-radio-group>
    </el-form-item>

    <!--民族-->
    <minority></minority>

    <el-form-item label="身份证号" prop="IDnumber">
      <el-input v-model="ruleForm.IDnumber"></el-input>
    </el-form-item>
    <el-form-item label="出生年月日" required>
      <el-col :span="11">
        <el-form-item prop="date1">
          <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
        </el-form-item>
      </el-col>
    </el-form-item>
    <el-form-item label="入学年份：" prop="schoolYear">
      <el-input v-model="ruleForm.schoolYear"></el-input>
    </el-form-item>
    <el-form-item label="是否是独生子女：" prop="isOneChile">
      <el-radio-group v-model="ruleForm.isOneChile">
        <el-radio label="是"></el-radio>
        <el-radio label="否"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="是否住校" prop="isZhuXiao">
      <el-radio-group v-model="ruleForm.isZhuXiao">
        <el-radio label="是"></el-radio>
        <el-radio label="否"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="监护人：" prop="guardian">
      <el-select v-model="ruleForm.guardian" placeholder="请选择监护人">
        <el-option label="爸爸" value="高中"></el-option>
        <el-option label="妈妈" value="专科"></el-option>
      </el-select>
    </el-form-item>

    <h1>家庭信息</h1>
    <el-form-item label="父亲学历" prop="fatherDucation">
      <el-select v-model="ruleForm.fatherDucation" placeholder="请选择父亲学历">
        <el-option label="高中" value="高中"></el-option>
        <el-option label="专科" value="专科"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="父亲职业" prop="fatherCareer">
      <el-select v-model="ruleForm.fatherCareer" placeholder="请选择父亲学历">
        <el-option label="医生" value="高中"></el-option>
        <el-option label="工程师" value="专科"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="电话" prop="fatherPhone">
      <el-input v-model="ruleForm.fatherPhone"></el-input>
    </el-form-item>

    <el-form-item label="母亲学历：" prop="motherDucation">
      <el-select v-model="ruleForm.motherDucation" placeholder="请选择母亲学历">
        <el-option label="高中" value="高中"></el-option>
        <el-option label="专科" value="专科"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="母亲职业：" prop="motherCareer">
      <el-select v-model="ruleForm.motherCareer" placeholder="请选择母亲职业">
        <el-option label="医生" value="高中"></el-option>
        <el-option label="工程师" value="专科"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="电话：" prop="motherPhone">
      <el-input v-model="ruleForm.motherPhone"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
      <el-button @click="resetForm('ruleForm')">取消</el-button>
    </el-form-item>
  </el-form>

  </div>
</template>

<script>
  import Minority from '@/pages/components/Minority'
  export default {
    data() {
      return {
        ruleForm: {
          sid:'',
          name:'',
          sex:'',
          nation:'',
          IDnumber:'',
          date1:'',    //出生日期
          schoolYear:'',
          isOneChile:'',
          isZhuXiao:'',
          guardian:'',
          fatherDucation:'',
          fatherCareer:'',
          fatherPhone:'',
          motherDucation:'',
          motherCareer:'',
          motherPhone:'',
        },
        rules: {
          studentNumber: [
            { required: true, message: '请输入学籍号', trigger: 'blur' },
            { min: 10, max: 10, message: '请输入10个字符', trigger: 'blur' }
          ],
          name: [
            { required: true, message: '请输入姓名', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' },
            { pattern:/^[A-Za-z0-9]+$/, message: '用户名只能为字母和数字'}
          ],
          nation: [
            { required: true, message: '请输入民族', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          sex: [
            { required: true, message: '请选择性别', trigger: 'change' }
          ],
          IDnumber: [
            { required: true, message: '请输入身份证号', trigger: 'blur' },
            { min: 18, max: 18, message: '请输入18个字符', trigger: 'blur' }
          ],
          date1: [
            { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
          ],
//          date2: [
//            { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
//          ],
          fathereducation : [
            { required: false, message: '请选择父亲学历', trigger: 'change' }
          ],
          fatherprofession : [
            { required: false, message: '请选择父亲职业', trigger: 'change' }
          ],
          phone1: [
            { required: false, message: '请输入电话号', trigger: 'blur' },
            { min: 11, max: 11, message: '请输入11个数字', trigger: 'blur' }
          ],
          mathereducation : [
            { required: false, message: '请选择父亲学历', trigger: 'change' }
          ],
          matherprofession : [
            { required: false, message: '请选择父亲职业', trigger: 'change' }
          ],
          phone2: [
            { required: false, message: '请输入电话号', trigger: 'blur' },
            { min: 11, max: 11, message: '请输入11个数字', trigger: 'blur' }
          ],
          desc: [
            { required: false, message: '请填写活动形式', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
     submitForm(ruleForm) {
      console.log(this.ruleForm)
     this.$ajax('/student/saveOne',this.ruleForm)
    .then(res => {
      console.log(res.data)
      this.orgListsData = res.data
      alert(orgListsData)
    });
  },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
    components:{
      Minority
    }
  }
</script>



